@import 'styles/variables';
@import 'styles/mixins';

.container {
  display: flex;
  align-items: center;
  padding: 16px 64px;

  .appInfo {
    display: flex;
    margin-left: 32px;

    .menuIcon {
      display: none;
      width: 32px;
      margin-left: 16px;
    }

    .logo {
      width: 32px;
      margin-left: 8px;

      &.appLogoSize1 {
        width: 24px;
      }
      &.appLogoSize2 {
        width: 32px;
      }
      &.appLogoSize3 {
        width: 36px;
      }
    }

    h1 {
      font-size: 20px;
      font-weight: 600;

      &.appNameSize1 {
        font-size: 16px;
      }
      &.appNameSize2 {
        font-size: 20px;
      }
      &.appNameSize3 {
        font-size: 24px;
      }

      &.appNameColor1 {
        color: $textColor1;
      }
      &.appNameColor2 {
        color: $primaryColor;
      }
    }
  }

  .menu {
    flex: 1;
    display: flex;

    &.menuPosition1 {
      justify-content: initial;
    }
    &.menuPosition2 {
      justify-content: center;
    }

    .item {
      color: $textColor1;
      font-size: 12px;
      margin: 0 12px;
      position: relative;
      cursor: pointer;

      &:hover {
        span {
          color: $textColorFade;
        }

        .itemContainer {
          .toggleIcon {
            filter: invert(43%) sepia(13%) saturate(491%) hue-rotate(171deg)
              brightness(97%) contrast(84%);
            opacity: 0.6;
          }
        }
      }

      .itemContainer {
        .toggleIcon {
          margin-right: 4px;
        }
      }

      .group {
        background-color: $white;
        display: flex;
        flex-direction: row;
        position: absolute;
        z-index: 2;
        border-radius: 4px;
        visibility: hidden;
        opacity: 0;
        @include boxShadow15b;

        &.menuGroupPosition1 {
          left: 0;

          &.visible {
            transform: translate(calc(-100% + 50px), 10px);
          }
        }
        &.menuGroupPosition2 {
          left: 50%;
        }

        &.menuGroupType1 {
          top: calc(100%);
          padding: 4px 0;
          white-space: nowrap;
        }
        &.menuGroupType2,
        &.menuGroupType3,
        &.menuGroupType4 {
          top: calc(100%);
          padding: 4px 0;
          white-space: nowrap;
          min-width: 100px;

          .groupItem {
            justify-content: flex-start;
            padding: 0 16px 0 32px;
          }
        }
        &.menuGroupType3,
        &.menuGroupType4 {
          .groupItem {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            padding: 8px 16px 8px 32px;

            .subTitle {
              color: $textColor2;
              font-size: 10px;
            }
          }
        }

        &.menuGroupAnimation1 {
          transform: translate(-50%, 20px);
          transition: all 0.3s;
        }
        &.menuGroupAnimation2 {
          transform: translateX(-50%);
          transition: 0.3s;
        }

        &.visible {
          visibility: visible;
          transform: translate(-50%, 10px);
          opacity: 1;
        }

        span {
          color: $textColor1;
          font-size: 12px;
          margin: 0 8px;
          cursor: pointer;
        }

        .groupItem {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 0 32px;
          min-height: 30px;

          &:hover {
            background-color: rgba(0, 0, 0, 0.03);
          }
        }

        .imageContainer {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 120px;
          padding: 32px;
        }
      }
    }
  }

  .buttons {
    display: flex;

    .button {
      margin-left: 8px;

      &:last-child {
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 1080px) {
  .container {
    padding: 16px 32px;
  }
}

@media screen and (max-width: 860px) {
  .container {
    justify-content: space-between;

    .appInfo {
      .menuIcon {
        display: block;
      }
    }

    .menu {
      display: none;
    }
  }
}
