@import 'styles/variables';

.mobile-menu {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  pointer-events: none;
  z-index: 1000;

  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    will-change: opacity;
    pointer-events: none;
    transition: opacity 0.3s cubic-bezier(0, 0, 0.3, 1);
  }

  &.menu--visible {
    pointer-events: auto;

    &.menu--animatable {
      .app-menu {
        transition: all 330ms ease-out;
      }
    }

    &.mobile-menu {
      &:after {
        opacity: 1;
        pointer-events: auto;
      }
    }

    .app-menu {
      -webkit-transform: none;
      transform: none;
    }
  }

  &.menu--animatable {
    .app-menu {
      transition: all 130ms ease-in;
    }
  }

  .app-menu {
    background-color: $white;
    position: relative;
    max-width: 80vw;
    width: 90%;
    height: 100%;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
    transform: translateX(103%);
    display: flex;
    flex-direction: column;
    will-change: transform;
    z-index: 1010;
    pointer-events: auto;

    &.mobileSidebar2 {
      max-width: 100vw;
      width: 100%;
    }

    .close-icon {
      margin: 32px;
      width: 24px;
      height: 24px;
      filter: invert(70%) sepia(81%) saturate(0%) hue-rotate(85deg)
        brightness(101%) contrast(88%);
    }

    .content {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 300px;
      text-align: center;
      font-size: 11px;
    }
  }
}
