@import 'styles/variables';
@import 'styles/mixins';

.container {
  position: relative;
  top: 0;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 12px;
  white-space: nowrap;
  cursor: pointer;

  &:hover {
    &.buttonAnimation1 {
      top: -2px;
      transition: top 0.2s;
    }

    &:not(.transparent) {
      @include boxShadowBottom15b;
    }

    &.transparent {
      color: $textColorFade;
    }
  }

  &.primary {
    background-color: $primaryColor;
    color: $white;
  }
}
