@import 'styles/variables';
@import 'styles/mixins';

.container {
  min-height: 100vh;

  .heading {
    display: flex;
    align-items: center;
    padding: 0 64px;
    min-height: calc(100vh - 300px);

    .title {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .header {
        text-align: right;

        h1 {
          margin-bottom: 8px;
          font-size: 30px;
          font-weight: 500;
        }

        h2 {
          color: $textColor2;
          margin-bottom: 16px;
          font-size: 16px;
          font-weight: 500;
        }

        .button {
          width: fit-content;
        }

        .inputGroup {
          display: flex;

          input[type='text'] {
            height: 32px;
            border: 1px solid $primaryColor;
            border-radius: 0 4px 4px 0;
            padding: 0 16px 0 0;
            margin-left: -1px;
            font-family: iransansx;
            font-size: 12px;
            outline: none;
          }

          .inputGroupButton {
            border-radius: 4px 0 0 4px;

            &:hover {
              top: initial;
              transition: initial;
            }
          }
        }

        .jumpToSection {
          color: $textColor3;
          margin-top: 8px;
          font-size: 11px;
          cursor: pointer;

          &:hover {
            color: $textColor1;
          }
        }
      }
    }

    .content {
      flex: 1;
    }
  }

  .marketsContainer {
    padding: 64px;
    font-size: 12px;

    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .tabs {
        display: flex;

        .tab {
          color: $textColor3;
          background-color: transparent;
          padding: 10px 20px;
          z-index: 99;
          border-radius: 4px 4px 0 0;
          font-size: 14px;
          cursor: pointer;

          &:hover {
            background-color: rgba(0, 0, 0, 0.025);
          }

          &.active {
            color: $primaryColor;
            font-weight: 600;
            border-bottom: 2px solid $primaryColor;
          }
        }
      }
    }

    .tableContainer {
      background-color: $white;
      padding: 32px;
      border-radius: 4px 0 4px 4px;
      overflow: hidden;
      @include boxShadow15b;

      &:hover {
        @include boxShadow30b;
      }

      table {
        width: 100%;
        border-collapse: collapse;

        thead {
          tr {
            th {
              padding-bottom: 12px;
              text-align: right;
              white-space: nowrap;
              font-weight: 300;
            }
          }
        }

        tbody {
          tr {
            // &:hover {
            //   background-color: rgba(0, 0, 0, 0.03);
            // }

            td {
              padding: 12px 0;

              &:last-child {
                width: 1px;
              }

              .cellContainer {
                display: flex;

                .coinLogo {
                  width: 32px;
                  margin-left: 8px;
                }

                .description {
                  display: flex;
                  flex-direction: column;

                  .title {
                    font-size: 14px;
                    font-weight: 600;
                    white-space: nowrap;
                  }
                  .subTitle {
                    color: $textColorFade;
                    text-decoration: none;
                    font-size: 11px;
                    white-space: nowrap;
                  }
                }

                .priceChanges {
                  color: $white;
                  background-color: #f84960;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border-radius: 10px;
                  direction: ltr;
                  height: 30px;
                  width: 80px;

                  &.positive {
                    background-color: #02c076;
                  }
                }

                .priceChart {
                  height: 50px;
                }
              }
            }
          }
        }
      }
    }

    .footer {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 43.33px;

      .more {
        color: $textColor2;
        cursor: pointer;

        &:hover {
          color: $textColor1;
        }
      }
    }
  }

  .promotionContainer {
    margin: 0 64px 32px;
    overflow: hidden;

    .slider {
      display: flex;
      width: calc(100vw - 128px);
      overflow-x: scroll;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
      cursor: grab;

      &::-webkit-scrollbar {
        display: none; /* Chrome, Safari and Opera */
      }

      .promotion {
        display: inline-block;
        height: 160px;
        min-width: 280px;
        margin-left: 16px;
        overflow: hidden;

        img {
          height: 100%;
          width: 100%;
          border-radius: 4px;
        }
      }
    }
  }

  .exchangeContainer {
    display: flex;
    padding: 64px;
    font-size: 12px;

    .description {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;

      .texts {
        text-align: right;

        h2 {
          margin-bottom: 8px;
          font-size: 26px;
          font-weight: 500;
        }

        h3 {
          color: $textColor2;
          margin-bottom: 16px;
          font-size: 16px;
          font-weight: 500;
        }

        .imageContainer {
          width: 200px;
          height: 200px;
        }
      }
    }

    .content {
      flex: 1;
      display: flex;
      justify-content: center;

      .calculatorContainer {
        background-color: $white;
        width: 50%;
        max-width: 320px;
        border-radius: 4px;
        padding: 16px;
        @include boxShadow30b;

        form {
          display: flex;
          flex-direction: column;
          align-items: center;

          .picker {
            margin-bottom: 12px;
            width: 100%;
          }

          .inputContainer {
            position: relative;
            margin-bottom: 12px;
            width: 100%;

            input {
              color: #3e4548;
              background-color: $white;
              display: flex;
              padding: 16px;
              width: calc(100% - 34px);
              position: relative;
              border: 1px solid #e6e8ec;
              border-radius: 4px;
              font-size: 14px;
              direction: ltr;
            }

            label {
              color: $textColor3;
              position: absolute;
              top: 18.665px;
              right: 16px;
            }
          }

          .button {
            top: initial;
            transition: initial;
            width: calc(100% - 32px);
            margin-top: 16px;
            text-align: center;
          }

          .reverse {
            width: 24px;
            height: 24px;
            margin: 8px 0 16px;
            filter: invert(70%) sepia(81%) saturate(0%) hue-rotate(85deg)
              brightness(101%) contrast(88%);
            cursor: pointer;

            &:hover {
              filter: none;
            }
          }
        }
      }
    }
  }

  .informationContainer {
    display: flex;
    padding: 0 64px;
    font-size: 12px;
    min-height: calc(100vh - 300px);

    &:nth-child(even) {
      flex-direction: row-reverse;
    }

    .context {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      h2 {
        margin-bottom: 8px;
        font-size: 30px;
        font-weight: 500;
      }

      h3 {
        color: $textColor2;
        margin-bottom: 16px;
        font-size: 16px;
        font-weight: 500;
      }
    }

    .image {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;

      .imageContainer {
        width: 50%;
      }
    }
  }

  .unfinished {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
    text-align: center;
    font-size: 11px;
  }

  footer {
    color: $white;
    background-color: #535353;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
    text-align: center;
    font-size: 11px;
  }
}

@media screen and (max-width: 1024px) {
  .container {
    .exchangeContainer {
      .content {
        .calculatorContainer {
          width: 66%;
        }
      }
    }

    .informationContainer {
      .image {
        .imageContainer {
          width: 66%;
        }
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .container {
    .marketsContainer {
      .tableContainer {
        table {
          thead {
            tr {
              th {
                &:nth-last-child(2) {
                  display: none;
                }
              }
            }
          }
          tbody {
            tr {
              td {
                &:nth-last-child(2) {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 860px) {
  .container {
    .heading {
      flex-direction: column-reverse;
      padding: 0 32px;

      .title {
        justify-content: flex-start;

        .header {
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;

          .inputGroup {
            justify-content: center;
          }
        }
      }

      .content {
        flex: 1;
      }
    }

    .marketsContainer {
      padding: 32px;
    }

    .promotionContainer {
      margin: 0 32px 32px;

      .slider {
        width: calc(100vw - 64px);
      }
    }

    .exchangeContainer {
      flex-direction: column;
      padding: 32px;

      .description {
        .texts {
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;
          margin-bottom: 24px;
        }
      }

      .content {
        .calculatorContainer {
          width: 100%;
        }
      }
    }

    .informationContainer {
      flex-direction: column-reverse;
      min-height: initial;
      margin-bottom: 64px;
	  text-align: center;

      &:last-child() {
        margin-bottom: 0;
      }

      &:nth-child(even) {
        flex-direction: column-reverse;
      }
    }
  }
}

@media screen and (max-width: 780px) {
  .container {
    .marketsContainer {
      .tableContainer {
        padding: 32px 16px;

        table {
          thead {
            tr {
              th {
                &:last-child {
                  display: none;
                }
              }
            }
          }
          tbody {
            tr {
              td {
                &:last-child {
                  display: none;
                }
                &:nth-last-child(3) {
                  width: 1px;
                }

                .cellContainer {
                  .priceChanges {
                    width: fit-content;
                    padding: 0 12px;
                  }

                  .description {
                    .title {
                      font-size: 12px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .exchangeContainer {
      .content {
        .calculatorContainer {
          max-width: initial;
        }
      }
    }
  }
}
